body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.board {
  width: 500px;
  height: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.board-row {
	display: block;
	width: 500px;
	height: 125px;
}

.square {
	width: 125px;
	height: 125px;
	display: inline;
	border: 1px solid #f00;
	background: #fff;
	float: left;
	font-size: 80px;
	font-weight: bold;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}

.blankSquare {
	width: 125px;
	height: 125px;
	display: inline;
	border: 1px solid #f00;
	background: #000;
	float: left;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
}

.top {
	display: -webkit-flex;
	display: flex;
	text-align: center;
	width: 500px;
	height: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	border: 1px solid blue;
}

.banner {
	display: -webkit-inline-flex;
	display: inline-flex;
	text-align: center;
	width: 300px;
	height: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.newgame {
	display: -webkit-inline-flex;
	display: inline-flex;
	text-align: center;
	width: 200px;
	height: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

h1 {
	font-size: 25px;
}

.refresh {
	height: 40px;
	border: 1px solid #f00;
	background: #fff;
	font-size: 25px;
	font-weight: bold;
	text-align: center;
	padding: 5px 15px 5px 15px;
	box-sizing: border-box;
	margin-left: 20px;
}

